/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.pipeline-loading-icon {
  display: block;
  width: 40px;
  height: 40px;
  opacity: 0;
  pointer-events: none; }
  .pipeline-loading-icon--visible {
    opacity: 1;
    transition-delay: 0.5s; }
  .pipeline-loading-icon path {
    fill: none;
    stroke-width: 8px; }
  .pipeline-loading-icon path:nth-of-type(1) {
    animation: stroke-spin1 2s infinite linear;
    stroke: white;
    stroke-dasharray: 62.5, 437.5; }
  .pipeline-loading-icon path:nth-of-type(2) {
    animation: stroke-spin2 2s infinite linear;
    stroke-dasharray: 437.5, 62.5;
    stroke: #ffbc00; }

@keyframes stroke-spin1 {
  from {
    stroke-dashoffset: 250; }
  to {
    stroke-dashoffset: -250; } }

@keyframes stroke-spin2 {
  from {
    stroke-dashoffset: 187.5; }
  to {
    stroke-dashoffset: -312.5; } }
