/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.details-dataset {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  padding: 5em 0 0 5em; }
  .details-dataset--single {
    padding: 5em 0 0 9em; }

.details-dataset__accordion {
  margin-bottom: 36px;
  max-width: 75em;
  width: 100%; }

.details-dataset__accordion-header {
  margin-bottom: 16px; }

.details-dataset__row {
  display: flex;
  font-size: 1.4em; }
  .details-dataset__row span:nth-child(2),
  .details-dataset__row span:nth-child(3) {
    margin-right: 130px; }
  .details-dataset__row span:last-of-type {
    margin-right: 50px; }

.details-dataset__name-header,
.details-dataset__value-header {
  color: var(--color-text-faded);
  font-size: 13px; }

.details-dataset__label,
.details-dataset__value,
.details-dataset__value-header {
  margin-bottom: 8px; }

.details-dataset__label,
.details-dataset__name-header {
  display: block;
  flex-shrink: 0;
  width: 230px; }
  .details-dataset__label--single,
  .details-dataset__name-header--single {
    width: 50%; }

.details-dataset__value,
.details-dataset__value-header {
  display: flex;
  flex-shrink: 0;
  min-height: 20px;
  width: 310px;
  word-break: break-all; }
  .details-dataset__value--single,
  .details-dataset__value-header--single {
    width: 50%; }

.dataset-arrow-icon {
  fill: #00bcff;
  height: 1.8em;
  margin-left: 1em;
  width: 1.8em; }

.details-dataset__visualization-wrapper,
.details-dataset__image-container {
  cursor: pointer;
  opacity: 1;
  transition: 0.2s opacity ease; }
  .details-dataset__visualization-wrapper:hover,
  .details-dataset__image-container:hover {
    opacity: 0.85; }

.details-dataset__image-container {
  height: 188px; }

.details-dataset__image {
  max-width: 250px;
  width: 100%; }

.details-dataset__empty-plot {
  align-items: center;
  background-color: var(--color-bg-1);
  border: 1px solid var(--color-bg-5);
  display: flex;
  height: 188px;
  justify-content: center;
  width: 250px; }
