/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.pipeline-warning {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  left: 0;
  position: absolute;
  text-align: center;
  top: 0;
  transform: translateX(56px);
  transition: transform 0.4s ease, width ease 0.4s;
  width: calc((100% - 56px)); }
  @media (min-width: 700px) {
    .pipeline-warning--sidebar-visible {
      width: calc((100% - 400px));
      transform: translateX(400px); } }

.pipeline-warning__title, .pipeline-warning__subtitle {
  margin: 0 auto;
  width: 90%; }

@-webkit-keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.pipeline-warning__title {
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
  font-size: 2.8em;
  font-weight: normal;
  margin-bottom: 0.6em; }
  @media (min-width: 700px) {
    .pipeline-warning__title {
      font-size: 3.8em; } }

.pipeline-warning__subtitle {
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
  font-size: 1.4em;
  margin-bottom: 2.4em;
  max-width: 30em; }
  @media (min-width: 700px) {
    .pipeline-warning__subtitle {
      font-size: 1.5em; } }

.pipeline-warning .button:first-of-type {
  margin-bottom: 1.2em; }
