/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.kui-theme--light {
  --color-detail-bg: #e6e8eb;
  --color-header-bg: #ffbc00;
  --color-link: #076e9c;
  --color-subtext: rgba(0, 0, 0, 0.55);
  --color-text: #f8f9fa; }

.kui-theme--dark {
  --color-detail-bg: #1d303b;
  --color-header-bg: #ffbc00;
  --color-link: #00bcff;
  --color-subtext: rgba(255, 255, 255, 0.55);
  --color-text: #081c28; }

.update-reminder-unexpanded {
  background-color: var(--color-header-bg);
  display: flex;
  font-size: 12px;
  height: 32px;
  justify-content: space-around;
  position: absolute;
  right: 0px;
  top: 0px;
  width: 296px;
  z-index: 2; }
  .update-reminder-unexpanded p {
    color: #081c28;
    text-align: center;
    margin-top: 7.5px;
    font-size: 12px; }
  .update-reminder-unexpanded .buttons-container {
    margin-top: 6.5px; }
  .update-reminder-unexpanded button {
    background-color: var(--color-header-bg);
    border: none;
    color: rgba(0, 0, 0, 0.55);
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    transition: color 0.2s ease; }
    .update-reminder-unexpanded button:hover {
      color: rgba(0, 0, 0, 0.85); }

.update-reminder-expanded-header {
  background-color: var(--color-header-bg);
  display: flex;
  font-size: 12px;
  height: 48px;
  justify-content: space-between;
  position: absolute;
  right: 0px;
  top: 0px;
  width: 484px;
  z-index: 2; }
  .update-reminder-expanded-header .close-button-container {
    margin-right: 20px; }
  .update-reminder-expanded-header p {
    color: #081c28;
    font-size: 16px;
    margin-top: 6px;
    padding: 6px 48px; }
  .update-reminder-expanded-header button {
    color: #081c28;
    border: none;
    background-color: var(--color-header-bg);
    font-size: 16px;
    padding: 0 0 5px 24px;
    cursor: pointer; }

.update-reminder-expanded-detail {
  background-color: var(--color-detail-bg);
  position: absolute;
  right: 0px;
  top: 48px;
  width: 484px;
  z-index: 2; }
  .update-reminder-expanded-detail p {
    font-size: 14px;
    padding-left: 48px;
    padding-top: 30px;
    width: 430px; }
    .update-reminder-expanded-detail p a {
      color: var(--color-link); }
  .update-reminder-expanded-detail .subtext {
    color: var(--color-subtext);
    font-size: 13px;
    margin-bottom: 0;
    padding-bottom: 5px;
    padding-top: 20px; }
    .update-reminder-expanded-detail .subtext--small {
      font-size: 12px;
      margin: 0 0 50px; }
  .update-reminder-expanded-detail .command-copier {
    font-size: 14px;
    padding-left: 48px;
    width: 400px; }

.close-button svg {
  fill: rgba(0, 0, 0, 0.85);
  height: 24px;
  width: 24px; }
