/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.pipeline-search-list {
  padding: 2px 1.4em 0 1.4em; }
  .pipeline-search-list .search-bar,
  .pipeline-search-list .search-input-wrapper {
    max-width: none; }
  .pipeline-search-list .search-bar .icon--close {
    height: 20px;
    right: 2px;
    top: 18px;
    width: 20px;
    z-index: 1; }
  .pipeline-search-list .search-bar .icon__graphics {
    opacity: 0.45;
    fill: var(--color-default-alt) !important; }
  .pipeline-search-list .search-bar--focused .icon__graphics {
    opacity: 0.75; }
  .pipeline-search-list .search-input {
    padding: 5px; }
  .pipeline-search-list .search-input:active:not(.search-input--disabled),
  .pipeline-search-list .search-input:hover:not([disabled]),
  .pipeline-search-list .search-input,
  .pipeline-search-list .search-input--focused,
  .pipeline-search-list .search-input__field {
    background: none;
    border: none;
    box-shadow: none; }
  .pipeline-search-list .search-input--focused {
    position: relative;
    z-index: 1;
    outline-color: #00bcff; }
  .pipeline-search-list .search-input__field::placeholder {
    opacity: 1; }
  .pipeline-search-list .search-input__field::placeholder {
    color: var(--color-default-alt) !important;
    opacity: 0.5; }
  .pipeline-search-list .search-input__field:hover::placeholder {
    opacity: 0.7; }
  .pipeline-search-list .search-input__field:focus::placeholder {
    opacity: 0.95; }
  .pipeline-search-list .search-input__field {
    margin: 0 0 2px 32px;
    font-size: 1.6em; }
  .pipeline-search-list .search-bar__icon-wrapper .icon {
    top: 16px;
    left: 6px; }
  .pipeline-search-list .search-input__line:before {
    flex: 1;
    max-width: none; }
  .pipeline-search-list .search-input__line:before {
    background-color: var(--color-default-alt);
    opacity: 0.3;
    transition: background-color 0.3s, opacity 0.3s; }
  .pipeline-search-list .search-bar--focused .search-input__line:before {
    opacity: 0.55; }
  .pipeline-search-list .search-bar .search-input__line:not([data-value='']):before {
    background-color: #00bcff;
    opacity: 1; }
  .pipeline-search-list .search-input__line:after {
    display: none; }
