/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.input {
  background-color: unset;
  border: none;
  color: var(--color-text);
  cursor: pointer;
  font-family: inherit;
  text-decoration-color: transparent;
  transition: text-decoration-color 0.2s ease;
  width: 100%; }
  .input:focus-visible {
    outline: none;
    text-decoration: underline;
    text-decoration-color: #00bcff; }
  .input::placeholder {
    color: var(--color-text); }

.input--large {
  font-size: 18px;
  font-weight: 600; }

.input--small {
  font-size: 14px;
  font-weight: normal; }

.input {
  min-height: auto;
  resize: none; }
  .input--large {
    height: 24px; }
  .input--small {
    height: 20px; }

.input-character-count {
  align-items: baseline;
  color: var(--color-text-faded);
  display: flex;
  font-size: 16px;
  justify-content: flex-end;
  margin-bottom: 6px;
  width: 100%; }

.input-number-characters {
  color: var(--color-text); }
