/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.kui-theme--light {
  --color-default-alt: black;
  --color-text: rgba(0, 0, 0, 0.85);
  --color-text-alt: rgba(255, 255, 255, 0.85);
  --color-text-faded: rgba(0, 0, 0, 0.55);
  --color-bg-1: #ccd1d6;
  --color-bg-2: #e6e8eb;
  --color-bg-3: #f0f1f3;
  --color-bg-4: #ffffff;
  --color-bg-5: #dadee2;
  --color-bg-alt: #242525;
  --color-sidebar-background: #f0f1f3;
  --color-global-toolbar-background: #f8f9fa;
  --color-sidebar-border: rgba(0, 0, 0, 0.05);
  --color-sidebar-shadow: rgba(0, 0, 0, 0.07);
  --color-run-list-divider: rgba(0, 0, 0, 0.1);
  --color-run-list-hover: #e6e8eb;
  --color-base-20: rgba(0, 0, 0, 0.2);
  --color-black-10: inherit; }

.kui-theme--dark {
  --color-default-alt: white;
  --color-text: rgba(255, 255, 255, 0.85);
  --color-text-alt: rgba(0, 0, 0, 0.85);
  --color-text-faded: rgba(255, 255, 255, 0.55);
  --color-bg-1: #001521;
  --color-bg-2: #081c28;
  --color-bg-3: #081c28;
  --color-bg-4: #122531;
  --color-bg-5: #2a3b46;
  --color-bg-alt: white;
  --color-sidebar-background: #081c28;
  --color-global-toolbar-background: #001521;
  --color-sidebar-border: rgba(0, 0, 0, 0.2);
  --color-sidebar-shadow: rgba(0, 0, 0, 0.3);
  --color-run-list-divider: rgba(255, 255, 255, 0.1);
  --color-run-list-hover: #1d303b;
  --color-base-20: rgba(255, 255, 255, 0.2);
  --color-black-10: rgba(0, 0, 0, 0.1); }

.kedro {
  -webkit-font-smoothing: antialiased;
  font-family: 'Titillium Web', sans-serif;
  font-size: 10px;
  font-weight: 400;
  letter-spacing: 0.1px;
  line-height: 1.4; }

*,
:after,
:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }
