.pipeline-minimap-toolbar {
  display: block;
  list-style: none;
  margin: 0;
  padding: 1em 0; }

.kui-theme--light {
  --minimap-toolbar-active: #f0f1f3; }

.kui-theme--dark {
  --minimap-toolbar-active: #1d303b; }

.pipeline-minimap-toolbar {
  margin-top: auto; }

.pipeline-minimap-button {
  transition: background-color 300ms ease; }

.pipeline-minimap-button.pipeline-icon-toolbar__button--active svg {
  opacity: 1; }

.pipeline-minimap-button--map {
  padding-top: 4px; }

.pipeline-minimap-toolbar__scale {
  color: var(--color-text);
  display: block;
  font-size: 12px;
  font-weight: bold;
  margin-top: 16px;
  padding: 6px 8px 6px;
  text-align: center;
  width: 100%; }
