.kui-theme--light {
  --dropdown-shadow: rgba(0, 0, 0, 0.03);
  --dropdown-label-bg: #ffffff;
  --dropdown-border: rgba(0, 0, 0, 0.12);
  --dropdown-options-bg: #f0f1f3; }

.kui-theme--dark {
  --dropdown-shadow: rgba(0, 0, 0, 0.1);
  --dropdown-label-bg: #122531;
  --dropdown-border: color-dark;
  --dropdown-options-bg: #081c28; }

.dropdown {
  border: none;
  box-shadow: 0 1px 1px var(--dropdown-shadow);
  box-sizing: border-box;
  color: var(--color-text);
  display: block;
  position: relative; }

.dropdown--open {
  position: relative; }
  .dropdown--open .dropdown__label > .dropdown__icon {
    transform: rotate(0deg);
    transition: transform ease 0.2s, background ease 0.2s; }

.dropdown__label {
  align-items: center;
  background: var(--dropdown-label-bg);
  border-radius: 0;
  border: none;
  box-shadow: none;
  color: var(--color-text);
  cursor: pointer;
  display: flex;
  font-family: inherit;
  font-size: 1.6em;
  height: 40px;
  justify-content: space-between;
  letter-spacing: 0.1px;
  line-height: 1.25em;
  list-style: none;
  outline: 4px solid transparent;
  overflow: hidden;
  padding: 0 0 0 1.125em;
  position: relative;
  user-select: none;
  width: 100%;
  z-index: 8; }
  .dropdown__label:disabled {
    cursor: not-allowed; }
  .dropdown__label:focus:focus {
    outline: none;
    box-shadow: 0 0 0 4px #00bcff inset; }
    [data-whatinput='mouse'] .dropdown__label:focus:focus {
      box-shadow: none; }
  .dropdown__label .dropdown__icon {
    height: 24px;
    margin: 0 8px 0 12px;
    position: absolute;
    right: 0;
    transform: rotate(180deg);
    transform-origin: center;
    transition: transform ease 0.2s, background ease 0.2s;
    width: auto; }
    .dropdown__label .dropdown__icon > svg {
      fill: var(--color-default-alt);
      height: 24px;
      opacity: 0.5;
      width: 24px; }

.dropdown__options {
  z-index: 2;
  background: var(--dropdown-options-bg);
  border-top: none;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
  position: absolute;
  visibility: hidden;
  width: 100%;
  max-height: 300px;
  overflow: auto;
  margin: 0;
  transition: opacity ease 0.2s, transform ease 0.2s, visibility ease 0.2s;
  transform: translateY(-10px);
  opacity: 0; }
  .dropdown--open .dropdown__options {
    visibility: visible;
    transition: opacity ease 0.3s, transform ease 0.3s, visibility ease 0.3s;
    opacity: 1;
    transform: translateY(0); }
  .dropdown__options .dropdown--disabled {
    opacity: 0.5; }
  .dropdown__options > section {
    padding: 0; }
