/*
The below colours are defined based on the Kedro-viz style guide which can be found  
in the Style Guide docs under 'Theme Colours' 
*/
.kui-theme--light {
  --color-run-list-highlight: #0096cc;
  --color-run-list-bookmark: rgba(0, 0, 0, 0.85); }

.kui-theme--dark {
  --color-run-list-highlight: #00bcff;
  --color-run-list-bookmark: rgba(255, 255, 255, 0.85); }

.runs-list-card {
  align-items: flex-start;
  background: var(--color-bg-4);
  border-bottom: 1px solid var(--color-run-list-divider);
  display: flex;
  padding: 1.6em 2.8em 2em 3em; }
  .runs-list-card--active {
    background: var(--color-run-list-hover);
    box-shadow: -1px 0px 0px inset #00bcff; }
  .runs-list-card--disabled {
    opacity: 0.5;
    pointer-events: none; }
  .runs-list-card:hover {
    background: var(--color-run-list-hover);
    cursor: pointer; }
    .runs-list-card:hover .runs-list-card__bookmark--stroke {
      opacity: 1; }

.runs-list-card__title {
  font-size: 1.6em;
  line-height: 1.5; }
  .runs-list-card__title b {
    color: var(--color-run-list-highlight);
    font-weight: normal; }

.runs-list-card__gitsha {
  color: var(--color-text-faded);
  font-size: 1.3em;
  margin: 4px 0 12px; }
  .runs-list-card__gitsha b {
    color: var(--color-run-list-highlight);
    font-weight: normal; }

.runs-list-card__timestamp {
  font-size: 1.3em; }

.runs-list-card__notes {
  font-size: 1.3em;
  padding-top: 24px; }
  .runs-list-card__notes b {
    color: var(--color-run-list-highlight);
    font-weight: normal; }

.runs-list-card__checked {
  height: 24px;
  margin-right: 0.8em;
  width: 24px; }
  .runs-list-card__checked--comparing {
    fill: var(--color-base-20); }
  .runs-list-card__checked--active {
    fill: #00bcff; }

.runs-list-card__bookmark {
  fill: var(--color-run-list-bookmark);
  height: 24px;
  margin-left: auto;
  opacity: 1;
  transition: fill 300ms ease;
  width: 24px; }
  .runs-list-card__bookmark:hover {
    fill: var(--color-default-alt); }
  .runs-list-card__bookmark--stroke {
    opacity: 0;
    transition: fill 300ms ease; }
