.pipeline-run-plots-modal__back {
  padding: 0;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  background: none;
  border: none;
  box-shadow: none;
  cursor: pointer; }
  .pipeline-run-plots-modal__back:focus {
    outline: none; }
    [data-whatintent='keyboard'] .pipeline-run-plots-modal__back:focus {
      box-shadow: 0 0 0 3px rgba(0, 188, 255, 0.85) inset; }

.kui-theme--light {
  --color-bg-plot: #e6e8eb;
  --color-button-plot: #003464;
  --color-button-plot-hovered: #005d92;
  --color-button-text: white; }

.kui-theme--dark {
  --color-bg-plot: #081c28;
  --color-button-plot: #003464;
  --color-button-plot-hovered: #005d92;
  --color-button-text: white; }

.pipeline-run-plots-modal {
  align-items: center;
  background-color: var(--color-bg-plot);
  bottom: 0;
  display: flex;
  flex-direction: column;
  left: 80px;
  overflow-y: scroll;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 3; }

.pipeline-run-plots-modal__top {
  align-items: center;
  display: flex;
  flex-direction: row;
  min-width: 100%;
  padding: 4.5em 0 1.5em; }

.pipeline-run-plots-modal__header {
  display: flex;
  margin: 0 auto;
  padding: 0 32px; }

.pipeline-run-plots-modal__icon {
  display: inline-block;
  fill: var(--color-text);
  height: 2.8em;
  margin: 0 10px 0 0;
  width: 2.9em; }

.pipeline-run-plots-modal__title {
  font-size: 1.8em;
  margin-top: 1px; }

.pipeline-run-plots-modal__back {
  align-items: center;
  display: flex;
  margin: 0 -139px 0 58px;
  padding: 6px 0;
  position: relative; }

.pipeline-run-plots-modal__content {
  align-items: center;
  display: flex;
  margin: auto;
  width: calc(100% - 116px); }
  .pipeline-run-plots-modal__content--oneChart {
    justify-content: center; }
  .pipeline-run-plots-modal__content--twoCharts {
    justify-content: space-between; }
  .pipeline-run-plots-modal__content--plotly {
    height: calc(100% - 66px - 6em);
    margin: 3em auto 3em; }

.pipeline-run-plots-modal__back-text {
  font-size: 1.6em; }

.pipeline-run-plots-modal__back-icon {
  fill: var(--color-text);
  height: 1.4em;
  margin: 0 12px 0 0;
  width: 3.6em; }

.pipeline-run-plots__image-wrapper {
  margin-bottom: 3em;
  margin-top: 3em; }
  .pipeline-run-plots__image-wrapper--oneChart {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-height: 615px;
    max-width: 815px;
    width: 100%; }
  .pipeline-run-plots__image-wrapper--twoCharts {
    display: flex;
    flex-direction: column;
    width: 50%; }
    .pipeline-run-plots__image-wrapper--twoCharts:nth-of-type(2) {
      margin-left: 58px; }
  .pipeline-run-plots__image-wrapper--threeCharts {
    display: flex;
    flex-direction: column;
    width: 33.33%; }
    .pipeline-run-plots__image-wrapper--threeCharts:nth-of-type(2), .pipeline-run-plots__image-wrapper--threeCharts:nth-of-type(3) {
      margin-left: 35px; }
  .pipeline-run-plots__image-wrapper .pipeline-run-plots__image--oneChart,
  .pipeline-run-plots__image-wrapper .pipeline-run-plots__image--twoCharts,
  .pipeline-run-plots__image-wrapper .pipeline-run-plots__image--threeCharts {
    padding-bottom: 24px;
    width: 100%; }

.pipeline-run-plots__plot-wrapper {
  display: flex;
  flex-direction: column; }
  .pipeline-run-plots__plot-wrapper--oneChart {
    height: 100%;
    justify-content: center;
    width: 100%; }
  .pipeline-run-plots__plot-wrapper--twoCharts {
    width: 50%; }
    .pipeline-run-plots__plot-wrapper--twoCharts:nth-of-type(2) {
      margin-left: 47px; }
  .pipeline-run-plots__plot-wrapper--threeCharts {
    width: 33.333%; }
    .pipeline-run-plots__plot-wrapper--threeCharts:nth-of-type(2), .pipeline-run-plots__plot-wrapper--threeCharts:nth-of-type(3) {
      margin-left: 25px; }

.pipeline-run-plots-image-text-timestamp {
  font-size: 1.6em;
  padding-right: 1.6em; }

.pipeline-run-plots-image-text-relative_timestamp {
  font-size: 1.3em; }
